.TodoHeader {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    font-size: 30px;
    font-weight: 900;
    color: transparent;
    -webkit-text-stroke: 1px var(--title);
}

.TodoHeader-title {
    display: flex;
    flex-direction: row;
    align-items: center;
    text-align: center;
}

.TodoHeader-title--text {
    position: relative;
    left: 90px;
}

.TodoHeader-title--color {
    color: var(--title);
}

@media (max-width: 1800px) {
    .TodoHeader {
        width: 100%;
        font-size: 90%;
    }
}

@media (max-width: 890px) {
    .TodoHeader {
        font-size: 80%;
    }

    .TodoHeader-title {
        width: initial;
        margin: 10px;
        
    }

    .TodoHeader-title--text {
        position: relative;
        left: initial;
    }
}